/* Mensagens */
.message-wrapper {
    display: flex;
    margin-bottom: 12px;
    animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.message-avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 8px;
    margin-top: 4px;
}

.message-bubble {
    max-width: 60%;
    padding: 10px 14px;
    border-radius: 12px;
    font-size: 0.95rem;
    line-height: 1.5;
    word-wrap: break-word;
    position: relative;
}

/* Mensagem enviada (sua) */
.message-wrapper.sent {
    justify-content: flex-end;
}

.message-wrapper.sent .message-bubble {
    background: linear-gradient(135deg, #4a9eff, #3b82f6);
    color: #ffffff;
    border-bottom-right-radius: 4px;
}

/* Mensagem recebida */
.message-wrapper.received {
    justify-content: flex-start;
}

.message-wrapper.received .message-bubble {
    background: rgba(0, 168, 142, 0.15);
    color: #e2e8f0;
    border-bottom-left-radius: 4px;
    border: 1px solid rgba(0, 168, 142, 0.2);
}

/* Indicador de digitação */
.typing-indicator {
    display: flex;
    align-items: center;
    grid-gap: 6px;
    gap: 6px;
    padding: 12px 16px;
    background: rgba(0, 168, 142, 0.1);
    border-radius: 12px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 12px;
}

.typing-dot {
    width: 8px;
    height: 8px;
    background: #00a88e;
    border-radius: 50%;
    animation: typingAnimation 1.4s infinite;
}

.typing-dot:nth-child(2) {
    animation-delay: 0.2s;
}

.typing-dot:nth-child(3) {
    animation-delay: 0.4s;
}

@keyframes typingAnimation {

    0%,
    60%,
    100% {
        transform: translateY(0);
        opacity: 0.7;
    }

    30% {
        transform: translateY(-10px);
        opacity: 1;
    }
}

/* Input de mensagem */
.message-input-container {
    padding: 16px 20px;
    border-top: 1px solid rgba(100, 116, 139, 0.2);
    background: rgba(15, 17, 35, 0.5);
}

.message-input-wrapper {
    display: flex;
    grid-gap: 12px;
    gap: 12px;
    align-items: center;
}

.message-input {
    flex: 1 1;
    padding: 12px 16px;
    background: rgba(30, 35, 58, 0.8);
    border: 1px solid rgba(100, 116, 139, 0.3);
    border-radius: 10px;
    color: #ffffff;
    font-size: 0.95rem;
    outline: none;
    transition: all 0.3s ease;
    font-family: 'Work Sans', sans-serif;
    resize: none;
    max-height: 120px;
}

.message-input::placeholder {
    color: #64748b;
}

.message-input:focus {
    border-color: #00a88e;
    background: rgba(30, 35, 58, 0.95);
    box-shadow: 0 0 0 3px rgba(0, 168, 142, 0.1);
}

.send-button {
    padding: 12px 20px;
    background: #00a88e;
    color: white;
    border: none;
    border-radius: 10px;
    font-weight: 600;
    font-size: 0.9rem;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    grid-gap: 6px;
    gap: 6px;
}

.send-button:hover {
    background: #00c9a7;
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(0, 168, 142, 0.3);
}

.send-button:active {
    transform: translateY(0);
}

.send-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    transform: none;
}

/* Loading spinner */
.messages-loading {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: #00a88e;
}

/* Estado vazio */
